<template>
	<div class="shadow-explorer">

		<div class="samples">
			<div class="shadow-explorer-sample" :style="shadowCSS">This is the sample</div>
			<div class="shadow-explorer-sample" :style="shadowCSS">
				<h2>Title</h2>
				<p>This is the sample with a paragraph and title inside it. </p>
			</div>
		</div>


		<fieldset class="controls">
			<label>
				<span>Distance:</span>
				<input type="range" min="0" max="100" v-model="distance"/>
				{{ distance }}
			</label>

			<section v-for="(shadow, i) in shadows" :key="i">
				<h5 v-text="shadow.name"></h5>
				<label>
					<span>y:</span>
					<input type="text" v-model="shadow.y.formula" :class="{warning:!shadow.y.valid}"/>
					{{ shadow.computeValue('y', distance) }}
				</label>
				<label>
					<span>blur:</span>
					<input type="text" v-model="shadow.blur.formula" :class="{warning:!shadow.blur.valid}"/>
					{{ shadow.computeValue('blur', distance) }}
				</label>
				<label>
					<span>spread:</span>
					<input type="text" v-model="shadow.spread.formula" :class="{warning:!shadow.spread.valid}"/>
					{{ shadow.computeValue('spread', distance) }}
				</label>
				<label>
					<span>opacity:</span>
					<input type="text" v-model="shadow.opacity.formula" :class="{warning:!shadow.opacity.valid}"/>
					{{ shadow.computeValue('opacity', distance) }}
				</label>
			</section>

			<samp v-text="shadowCSS"></samp>
		</fieldset>
	</div>
</template>

<script>
class ShadowLayer {
	constructor({name, y, blur, spread, opacity}) {
		this.name = name || 'unnamed shadow'

		this.y = {
			formula: y || 'd', // Formula string to eval() y value, where 'd' is distance
			valid: true
		}
		this.blur = {
			formula: blur || 'd',
			valid: true
		}
		this.spread = {
			formula: spread || 'd',
			valid: true
		}
		this.opacity = {
			formula: opacity || 'd',
			valid: true
		}

	}

	computeValue(key, distance) {
		if (!this[key]) {
			console.error(`ShadowLayer.computeValue: key(${key}) is invalid`)
			return
		}
		const formula = this[key].formula
		distance = parseFloat(distance)

		try {
			var compute = new Function('d', `return ${formula}`)
			let value = compute(distance).toFixed(1)

			if (isFinite(value)) {
				return value
			} else {
				return 1
			}
		}
		catch(error) {
			return NaN
		}
	}
	computeCSS(distance) {
		const y = this.computeValue('y', distance),
			blur = this.computeValue('blur', distance),
			spread = this.computeValue('spread', distance),
			opacity = this.computeValue('opacity', distance),
			color = `hsla(0, 0%, 0%, ${opacity})`

		return `0 ${y}px ${blur}px ${spread}px ${color}`
	}
}



export default {
	name: 'ShadowExplorer',
	data() {
		return {
			distance: 5,
			shadows: []
		}
	},
	computed: {
		shadowCSS() {
			const layers = []

			for (let i=0; i<this.shadows.length; i++) {
				// console.log(this.shadows[i].name)
				// console.log(this.shadows[i].computeCSS(this.distance))
				layers.push( this.shadows[i].computeCSS(this.distance) )
			}

			return 'box-shadow: ' + layers.join(', ') + ';'
		}
	},
	created() {
		this.shadows.push(new ShadowLayer({
			name: 'cast shadow',
			y: 'Math.log(d+1) * 4',
			blur: '2 + Math.log(d+4) * 1.5',
			spread: '-2 - Math.log(d+1) * 3',
			opacity: 'Math.log(d+2) * 0.15'
		}))
		this.shadows.push(new ShadowLayer({
			name: 'ambient shadow',
			y: 'Math.log(d+1) * 2',
			blur: 'Math.log(d+1) * 8',
			spread: '-Math.log(d+1)',
			opacity: 'Math.log(d+2) * 0.03'
		}))
	}
}
</script>

<style>
	.shadow-explorer {
		display: flex;
		flex-wrap: wrap;
	}
	.shadow-explorer .controls,
	.shadow-explorer .samples {
		box-sizing: border-box;
	}
	@media only screen and (min-width: 600px) {

		.shadow-explorer .controls,
		.shadow-explorer .samples {
			display: inline-block;
			width: 50%;
		}
	}

	.shadow-explorer samp {
		display: block;
		overflow-wrap: break-word;
		height: 5em;
		overflow: auto;

		margin: 0.5em 0;
		font-size: 0.9em;
		color: #888;
	}
	.shadow-explorer label {
		display: block;
	}

	.shadow-explorer .warning,
	.shadow-explorer .warning:hover,
	.shadow-explorer .warning:active {
		border: 1px solid red;
	}

	.shadow-explorer-sample {
		display: block;
		box-sizing: border-box;

		margin: 1em 1em 3em 4em;
		padding: 0.5em;
		border: 1px solid #CCC;

		max-width: 20ch;
	}
</style>